
import { defineComponent, reactive } from "vue";

export default defineComponent({
  name: "HelloWorld",
  props: {
    geolocation: {
      type: String,
      require: false,
    },
    location: {
      type: String,
      require: true,
    },
    img: {
      type: String,
      require: true,
    },
    weather: {
      type: String,
      require: true,
    },
    temp: {
      type: String,
      require: true,
    },
  },
  setup(props, context) {
    const state = reactive({
      isLoadet: false,
    });
    const modalClose = () => {
      context.emit("modalClose");
    };
    return {
      state,
      modalClose,
    };
  },
});
